<template>
 <el-container class="container">
    <div class="head">
         <router-link to='/' class="backToHome">
         <img :src="homeimg" alt="">
         </router-link>
    </div>
     <div class="main">
        <el-table
            size='small '
            :data="itemlist"
            show-summary
            sum-text=" "
            :summary-method="getColumns"
            style="width: 100%"
            
            >
            <!-- :default-sort = "{prop: 'solutionVolume', order: 'descending'}" -->
            <el-table-column label="" type="index" show-overflow-tooltip width="100" style="margin-left:20px">
            </el-table-column>
            <!-- <el-table-column
            prop="tablename"
            label=""
            sortable>
            </el-table-column> -->
            <el-table-column
            prop="canLength"
            label=""
            sortable
            >
            </el-table-column>
            <el-table-column
            prop="canRadius"
            label=""
            sortable
            >
            </el-table-column>
            <el-table-column
            prop="solutionHeight"
            label=""
            sortable
            >
            </el-table-column>
            <el-table-column
            prop="solutionVolume"
            label=""
            sortable
            >
            </el-table-column >
            <el-table-column  align='center'>
            <template slot-scope="scope">
                <el-button
                size="mini"
                type="danger"
                plain
                icon="el-icon-delete"
                circle
                @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>
        <!--  -->
     <el-divider></el-divider>
     </div>
    <el-button
        class="dowload"
        size="small "
        plain
        round
        type='success  '
        icon="el-icon-download"
        @click="download">
    </el-button>
     
 </el-container>       
</template>

<script>
import { Message ,Loading } from 'element-ui';
export default {
    components:{
    },
    data(){
        return{
            itemlist:[],
            fileId:'',
            homeimg:'https://i.loli.net/2020/05/28/RGNa7VAbydutSjQ.png'
        }
    },
   created(){
       this.fileId = this.$route.query;
       //
       this.$axios.post('/api/fileItem/findOneFileitem',this.fileId).then(res=>{
           this.itemlist = res.data;
       })
   },
   methods:{
       handleDelete(index,row){
        const _this = this ;
        console.log(index,row.id);
        this.$axios.post('/api/fileItem/deleteItem',{id:row.id}).then(res=>{
                if(res.status == 200){
                    //    
                    _this.$axios.post('/api/fileItem/findOneFileitem',_this.fileId).then(r=>{
                        _this.itemlist = r.data;
                    })
                _this.$message({
                        message:"",
                            type:"success"
                        })
            }else if (res.status == 400){
                    this.$message.error('');
            }
        })
       },
       download(){
            this.$axios.post('/api/fileExport/Excel',this.fileId).then(res=>{
                const buf = Buffer.from(res.data.result, 'binary')
                var blob = new Blob([buf], {type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8'}); // application/vnd.openxmlformats-officedocument.spreadsheetml.sheetxlsx
                var downloadElement = document.createElement('a');
                var href = window.URL.createObjectURL(blob); // 
                downloadElement.href = href;
                var name = ''
                downloadElement.download =  name +'.xlsx'; // 
                document.body.appendChild(downloadElement);
                downloadElement.click(); // 
                document.body.removeChild(downloadElement); // 
                window.URL.revokeObjectURL(href); // blob  
            })
       },
    //    
       getColumns(param){
            const _this = this ;
            const {columns,data} = param;
            const sums = [];
            columns.forEach((column, index) => {
            if(index === 0) {
            sums[index] = ' ';
            return;
            }
            switch(column.property) {
            case "solutionVolume":
            sums[index] = _this.getSum(_this.itemlist)
            break;
            }
            });
            return sums;
       },

       getSum(list){
           var sum = 0
           list.forEach((ele,index)=>{
               sum += ele.solutionVolume
           })
            // return  Math.round(sum*100)/100
            //4
            return sum.toFixed(4);
       },
 }

}
</script>
 
 <style scoped>
 .container{
  background: #11998e; 
  background: -webkit-linear-gradient(to right top, #ccb182,#42bc93);  
  background: linear-gradient(to right top, #ccbf82,#42bc93);
  display: flex;
  width:100vw;
  justify-content: center;
 }
 .head{
     height: 14vh;
     width:90vw;
     position: fixed;
     top: 0;
     z-index:1000;
 }
 .dowload{
     position: fixed;
     bottom: 10%;
     right: 3%;
     z-index:1000;
     font-size: 17px;
 }
  .backToHome{
    width:5%
  }
  .backToHome img{
    width:6%;
    margin: 10px 0;
    padding: 0 !important;
  }
  .backToHome img::after{
    content: '';
    position:absolute;
  }
 .main{
     width: 75vw;
     margin-top:14vh;
 }
 .header-row{
     background:none;
     border: none;
     border-bottom: 2px solid #00634f;
     color: #00634f;
     font-weight: bold;

 }
 
 </style>